import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import GlobalFilter from '@/utils/filter/globalFilter'
import { GlobalMixIn } from '@/utils/mixins'
import RegisterComponents from '@/utils/registerComponents'
import Directive from '@/utils/directives'

Vue.config.productionTip = false


//自定义指令
Vue.use(Directive)


//编辑器
Vue.use(mavonEditor)

// Filter
Vue.use(GlobalFilter)

//全局mixin 
Vue.use(GlobalMixIn)

//注册所有组件
Vue.use(RegisterComponents)




new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')


